.skills {
  max-width: 800px;
  height: 540px;
  position: relative;
}

.lines {
  height: 100%;
  position: relative;
  margin-left: 80px;
}

.line {
  height: inherit;
  width: 2px;
  position: absolute;
  background-color: #d1d1d1;
}

.line.l--0 {
  left: 0;
}

.line.l--33 {
  left: 33%;
}

.line.l--66 {
  left: 66%;
}

.line.l--100 {
  left: calc(100% - 1px);
}

.line__label {
  display: none;
}

.charts {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}

.chart:not(:first-child) {
  margin-top: 10px;
}

.chart__title {
  display: inline-block;
  margin: 0 0 10px;
  font-weight: bold;
  background-color: #fff;
}

.skills ul li {
  list-style-type: none;
  margin-bottom: 4px;
}

.chart--horiz {
  overflow: hidden;
  padding-left: 80px;
}

.chart__bar {
  height: 20px;
  margin-bottom: 10px;
  background: linear-gradient(to bottom right, #7f017f, #330145);
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  opacity: 0;
  -webkit-animation: 1s anim-lightspeed-in ease forwards;
  animation: 1s anim-lightspeed-in ease forwards;
}

.chart--dev .chart__bar:nth-of-type(4) {
  -webkit-animation-delay: 1.3s;
  animation-delay: 1.3s;
}

.chart--dev .chart__bar:nth-of-type(3) {
  -webkit-animation-delay: 1.1s;
  animation-delay: 1.1s;
}

.chart--dev .chart__bar:nth-of-type(2) {
  -webkit-animation-delay: 0.9s;
  animation-delay: 0.9s;
}

.chart--dev .chart__bar:nth-of-type(1) {
  -webkit-animation-delay: 0.7s;
  animation-delay: 0.7s;
}

.chart--design .chart__bar:nth-of-type(3) {
  -webkit-animation-delay: 1.1s;
  animation-delay: 1.1s;
}

.chart--design .chart__bar:nth-of-type(2) {
  -webkit-animation-delay: 0.9s;
  animation-delay: 0.9s;
}

.chart--design .chart__bar:nth-of-type(1) {
  -webkit-animation-delay: 0.7s;
  animation-delay: 0.7s;
}

.chart__label {
  width: 80px;
  margin-left: -80px;
  line-height: 20px;
  font-size: 0.8rem;
  z-index: 2;
  background-color: #fff;
}

li:hover .chart__label {
  font-weight: 900;
}

li:hover .chart__bar {
  background: #000;
}

li:nth-child(odd) .chart__label {
  background-color: #eee;
}

@-webkit-keyframes anim-lightspeed-in {
  0% {
    -webkit-transform: translateX(-200%);
    transform: translateX(-200%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes anim-lightspeed-in {
  0% {
    -webkit-transform: translateX(-200%);
    transform: translateX(-200%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}

.flex-li {
  display: flex;
}

@media only screen and (min-width: 270px) {
  .line__label {
    display: block;
    font-size: 0.8rem;
    text-align: center;
    position: absolute;
    bottom: -20px;
    line-height: 1.5rem;
    right: -30px;
  }
  .line__label.title {
    text-transform: uppercase;
    font-weight: bold;
  }
}

@media only screen and (min-width: 560px) {
  .line__label {
    width: 108px;
    font-size: 1rem;
    right: -50px;
  }
}

@media only screen and (min-width: 620px) {
  .lines {
    margin-left: 100px;
  }
  .chart--horiz {
    padding-left: 100px;
  }
  .line__label {
    width: 120px;
  }
  .chart__label {
    width: 100px;
    margin-left: -100px;
    font-size: 1rem;
  }
}

@media only screen and (min-width: 1200px) {
  .lines {
    margin-left: 200px;
  }
  .skills {
    height: 490px;
  }
  .chart--horiz {
    padding-left: 200px;
  }
  .chart__label {
    width: 200px;
    margin-left: -200px;
    font-size: 1rem;
  }
}
